
import './index.scss'
import Head from '~/components/Head'
import { NoticeBar, Icon, List, TextareaItem, Stepper, PickerView, Picker, Button } from 'antd-mobile';
import { observer } from 'mobx-react'
import { connect } from 'react-redux'
import DataStore from '@/mobx/data'
import { failToast,successToast } from '&/methods'
import { borrowBook } from '../../actions';
import {history} from '&/history'

@connect(
  state => {
    return {

    }
  }
)



@observer

export default class BorrowBook extends Component {

  constructor() {
    super();
    this.state = {
      txm: require('@/assets/images/txm.jpg'),
      readerinfo: {},
      detail: false,
      tipMsg: '请输入书籍编号查询...',
      count: 1,
      text: ``,
      num:1
    }
  }

  //搜索书 id
  searchBook = () => {
    console.log(this.bookid.state.value)
    if (this.bookid.state.value.length == 0) {
      failToast('请输入编号', 1)
      return;
    }

    var that = this;

    DataStore.getBookInfoById({
      url: '/bookstore/getBookInfoById',
      params: {
        id: this.bookid.state.value
      }
    }, function (res) {
      console.log(res)
      if (res) {
        that.setState({
          detail: true,
          count: res.count
        })
      } else {
        that.setState({
          detail: false,
          tipMsg: '没有找到该书籍...'
        })
      }
    })
  }

  //保存借阅书籍
  saveBook = () => {
    // console.log(this.state.num);
    var readername = this.readername.state.value;
    var readertel = this.readertel.state.value;
    const {num} = this.state;
    const {bookid} = this.state.readerinfo;
    var bookname = DataStore.borrowbookinfo.name;
    // console.log(readername,readertel,num,bookid,bookname)
    if(readername.length==0){
      failToast('请输入姓名',0.8)
      return;
    }
    if(readertel.length==0){
      failToast('请输入电话号码',0.8)
      return;
    }

    if(num>DataStore.borrowbookinfo.count){
      failToast('请选择正确的数量',0.8);
      return;
    }

    this.props.dispatch(borrowBook({
      url:'/bookstore/borrowBook',
      params:{
        readername,readertel,num,bookid,bookname
      }
    })).then(res=>{
      DataStore.changBookCount(num);
      successToast('借书成功',800);
      setTimeout(()=>{
        history.push('/index/mode/borrow')
      },800)
    })


  }

  render() {
    const { readerinfo, detail, tipMsg, count ,text} = this.state;
    const { borrowbookinfo } = DataStore;
    // console.log(borrowbookinfo)
    return (
      <div className='borrowbook'>
        <Head title='借书管理' showBack={true}></Head>
        <div>
          <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
            注意：请如实填写以下信息。
            </NoticeBar>
        </div>
        <div className='borrowcon'>
          <div className='txm'>
            <div className='sys'>
              <img src={this.state.txm} alt="" />
              <div className='search'>
                <span className='iconfont icon-tubiaozhizuomoban'></span>
              </div>
            </div>
            <p>点击扫码借书</p>
          </div>

          <div>
            <div>
              <List className='booksearch'>
                <TextareaItem
                  ref={el => this.bookid = el}
                  title="书籍编号："
                  placeholder='(必填)'
                  autoHeight
                  required
                  labelNumber={5}
                  onChange={(val) => {
                    this.setState({
                      readerinfo: { ...readerinfo, bookid: val }
                    })
                  }
                  }
                />
                <div className='sousuo' onClick={this.searchBook}><span className='iconfont icon-sousuo'></span></div>
              </List>
            </div>

            {detail && <div style={{ display: detail ? 'block' : 'none' }}>
              <div className='searchlist1'>
                <p><span>书名：</span><span style={{ color: 'red' }}>{borrowbookinfo.name}</span></p>
                <p><span>编号：</span><span style={{ color: 'red' }}>{borrowbookinfo.id}</span></p>
                <p><span>库存：</span><span style={{ color: 'red' }}>{borrowbookinfo.count}</span> 本</p>
                <p>最多可借： <span style={{ color: 'red' }}>{borrowbookinfo.count}</span> 本</p>
              </div>
              <div>
                <List>
                  <TextareaItem
                    ref={el => this.readername = el}
                    title="姓名："
                    placeholder='(必填)'
                    autoHeight
                    required
                    labelNumber={5}
                    onChange={(val) => {
                      this.setState({
                        readerinfo: { ...readerinfo, readername: val }
                      })
                    }
                    }
                  />
                </List>
              </div>
              <div>
                <List>
                  <TextareaItem
                    ref={el => this.readertel = el}
                    title="手机号："
                    placeholder='(必填)'
                    autoHeight
                    required
                    labelNumber={5}
                    onChange={(val) => {
                      this.setState({
                        readerinfo: { ...readerinfo, readertel: val }
                      })
                    }
                    }
                  />
                </List>
              </div>
              <div>
                <List>
                  <List.Item extra={
                    <Stepper
                      style={{ width: '100%', minWidth: '100px' }}
                      showNumber
                      max={borrowbookinfo.count}
                      min={1}
                      defaultValue={1}
                      onChange={v => { this.setState({ num: v }) }}
                    />}
                  >
                    借阅数量：
        </List.Item>
                </List>
              </div>
              {/* <div style={{ padding: '15px', margin: '20px 0' }}>
                <p style={{ color: 'red' }}>以下为你的书籍借阅编号，还书时请提供该编号：</p>
                <div style={{backgroundColor:'yellow'}}>{text}</div>
              </div> */}
              <div className='borrowbtn'>
                <Button style={{ width: '90%' }} type='primary' onClick={this.saveBook}>确定</Button>
              </div>

            </div>}

            {!detail && <div style={{ display: detail ? 'none' : 'block', padding: '15px', color: 'red' }}>
              {tipMsg}
            </div>}


          </div>

          <div>

          </div>

        </div>
      </div>
    )
  }
}